<template>
  <v-list-group
    :group="group"
    :prepend-icon="item.icon"
    no-action
  >
    <template v-slot:activator>
      <v-list-item-content class="v-list-group__activator">
        <v-list-item-title>
          {{ item.text }}
        </v-list-item-title>
      </v-list-item-content>
    </template>
    <template v-for="(child, i) in children">
      <base-item
        :key="`item-${i}`"
        :chip="genChip(child)"
        :icon="child.icon"
        :subtext="child.subtext"
        :to="child.to"
        :text="child.text"
      />
    </template>
  </v-list-group>
</template>

<script>
  // Utilities
  import kebabCase from 'lodash/kebabCase' 
  export default {
    inheritAttrs: false,
    components: {
        BaseItem:() => import('./Item'),
    },
    props: {
      item: {
        type: Object,
        default: () => ({
          text: '',
          group: '',
          children: [],
        }),
      }
    },

    computed: {
      children () {
        // console.log(this.item);
        return this.item.children.map(item => ({
          ...item,
          to: `${this.item.group}/${item.to}`,
        }))
      },
      group () {
        let groupStr=this.genGroup(this.item.children)
        return groupStr
      },
    },

    methods: {
      genChip (item) {
        if (item.new) return 'new'
        if (item.updated) return 'updated'
        if (item.deprecated) return 'deprecated'
        if (item.help) return 'help'
        },
      genGroup (children) {
        return children.map(item => {
          const parent = item.group || this.item.group
          let group = `${kebabCase(item.to)}`
          if (parent){
              group = `${parent}/${kebabCase(item.to)}`
          }
          if (item.children) {
            group = `${group}|${this.genGroup(item.children)}`
          }
          return group
        }).join('|')
      },
    },
  }
</script>

<style>
.v-list-group__activator p {
  margin-bottom: 0;
}
</style>
